import React from 'react'
import ToDo from './ToDo'

export default function ToDoList({todos}) {
       console.log(todos)
       return (
              <div>
                 The List: 
                 <ul>
                     {
                            todos.map(todo => <ToDo todo={todo} />)
                     }
                 </ul>
              </div>
       )
}
